<template>
  <div class="container">
    <el-card v-for="item in rankList" :key="item.stuId">
        <el-row>
        <el-col :span="6">
          <el-image></el-image>
        </el-col>
        <el-col :span="12">
          <ul class="stuInfo">
            <li>学号：{{ item.stuId }}</li>
            <li> 打卡次数{{ item.stuId }}</li>
            <li>排名：{{ item.stuId }}</li>
          </ul>
        </el-col>

      </el-row>
    </el-card>
  </div>
</template>
<script>
export default {
    name: 'Rank',
    mouted() {
      // 获取排名
    let ajaxData = {
      url: "/api/user/cardpage",
      method: "get"
    };
    this.$axios(ajaxData)
      .then(res => {
        console.log(res);
        if (res.statusText == "OK") {
          return (this.rankList = res.data);
        }
      })
      .catch(err => {
        console.log(err);
      });
    },
    data() {
        return {
          rankList: [
            {
              stuId: 'S1'
            },
            {
              stuId: 'S2'
            },
            {
              stuId: 'S3'
            }
          ]
        }
    },
}
</script>
